import React, { Component } from 'react';

import '../animations.css';

import { Tooltip, Collapse  } from 'antd';

const Panel = Collapse.Panel;

class Item extends Component {
    // componentWillMount() {
    //     document.addEventListener('keydown', this.detectKeyPress.bind(this));
    // }

    // detectKeyPress = (event) => {
    //     const key = event.which || event.keyCode; // keyCode detection
    //     const ctrl = event.ctrlKey ? event.ctrlKey : ((key === 17) ? true : false); // ctrl detection
    //     if ( key === 67 && ctrl ) {
    //         console.log("Ctrl + C Pressed !");
    //     }
    //   }

    render() { 
        const indexItem = this.props.indexItem;
        const numChildCategory = this.props.numChildCategory;
        const currentTitre = this.props.currentTitre;
        const data = this.props.data;
        // console.log('data: ', data);
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data).map(item => item === currentTitre ? Object.keys(data[item]).map(category => Object.keys(data[item][category]).map(categoryItem => arrayData.push(data[item][category][categoryItem]))): null);
        // Object.keys(data).map(item => Object.keys(data[item]).map(category => Object.keys(data[item][category]).map(categoryItem => arrayData.push(data[item][category][categoryItem]))));
        // Object.keys(data).map(item => Object.keys(data[item]).map(category => arrayData.push(data[item][category])));
        // arrayData.reverse();
        // console.log('arrayData: ', arrayData);

        const liste = (arrayData || []).map((elt, id) =>
            <Collapse 
                defaultActiveKey={elt.titreCat} 
                key={`titreCat-${id}`} 
                style={{ marginBottom : '1em' }}
                id={`titreCat-${id}`}
                // className='item-container'
            >
                <Panel header={
                    <div>
                        <span>{elt.titreCat}</span> 
                        <input type='checkbox' id={elt.titreCat}/>
                        <button 
                        name="trash" 
                        className="trash" 
                        onClick={() => this.props.deleteCategory(elt, id)}>
                            <span role='img' aria-label="trash" className='trashImg' >
                                🗑️
                            </span>
                        </button>
                    </div>
                    } 
                    key={elt.titreCat} 
                    id={elt.titreCat} 
                    accordion={true}
                    // className='item-list'
                    >   
                    {   
                        (Object.keys(elt.categoryItems || [])).map((item, id) =>
                            <li className={indexItem === `elt-${elt.categoryItems[item].item}` && numChildCategory > 1 ? 'item-container message-leave': 'item-container'} id={`elt-${elt.categoryItems[item].key}`} key={elt.categoryItems[item].key}>
                            <ul className='item item-list'>
                            <Tooltip placement="topLeft" title='Valider' mouseEnterDelay={0.5} >
                                <button 
                                    className='checked' 
                                    onClick={() => this.props.isChecked(elt.categoryItems[item], id)}>
                                    <span role='img' aria-label="checked">
                                        {
                                            elt.categoryItems[item].checked ? 
                                            <span style={{color: '#009688', fontSize: '0.8em'}}>
                                                <i className="fas fa-check"/>
                                            </span>
                                            :
                                            <span style={{color: '#009688'}}>
                                             🗸
                                            </span>
                                        }
                                    </span>
                                </button>
                            </Tooltip>
                                <li className={elt.categoryItems[item].checked ? 'item flip' : 'item'} key={elt.categoryItems[item].key}>
                                    <span>
                                        {elt.categoryItems[item].item}
                                    </span>
                                    <Tooltip placement="top" title='Supprimer' mouseEnterDelay={0.5}>
                                        <button 
                                            name="trash" 
                                            className="trash" 
                                            onClick={() => this.props.delete(elt.categoryItems[item], id)}>
                                            <span role='img' aria-label="trash" className='trashImg' >
                                                🗑️
                                            </span>
                                        </button>
                                    </Tooltip>
                                    <Tooltip placement="top" title='Commentaire' mouseEnterDelay={0.5}>
                                        <button 
                                            name="comment" 
                                            className='comment' 
                                            onClick={() => this.props.toggleComment(elt.categoryItems[item])}>
                                            {
                                                elt.categoryItems[item].comment === '' ?
                                                <span style={{color: '#009688'}}>
                                                    <i className="far fa-comment-dots"/>
                                                </span>
                                                :
                                                <span style={{color: '#009688'}}>
                                                    <i className="fas fa-comment-dots"/>
                                                </span>
                                            }
                                        </button>
                                    </Tooltip>
                                </li>
                            </ul>
                            <form className='form-comm' onSubmit={e => this.props.closeComment(e, elt.categoryItems[item])}>
                                <input 
                                    value={elt.categoryItems[item].comment} 
                                    name='comment' 
                                    type='text'
                                    id='inputComment'
                                    placeholder='ajouter un commentaire'
                                    onChange={(e) => this.props.addComment(e, elt.categoryItems[item])}
                                    className={elt.categoryItems[item].show ? 'item message-enter-active' : 'item message-leave-active'}
                                    maxLength={50}
                                />
                            </form>
                        </li>
                        
                            )
                    }
                    </Panel>
                </Collapse>
        )

        return ( 
            <>
                {liste}
            </>
         );
    }
}
 
export default Item;